.workouts {
  width: 70%;
  height: 2100px;
  background-color: white;

  border-radius: var(--borderRadius);

  position: absolute;
  right: 40px;
  margin-bottom: 30px;
}

.gridContainer {
  width: 90%;
  height: 90%;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* background-color: grey; */
}
